// Override theme settings ----------------------------
// (see reveal.js/css/theme/template/settings.scss)

// Background of the presentation
$backgroundColor: #faecd6;

// Primary/body text
$mainFontSize: 36px;
$mainFont: "Cormorant Garamond", Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
$mainColor: #284368;

// Vertical spacing between blocks of text
//$blockMargin: 20px;

// Headings
//$headingMargin: 0 0 $blockMargin 0;
$headingFont: "Trajan Pro", "Cormorant Garamond", Georgia, 'Liberation Serif', serif;
$headingColor: #284368;
//$headingLineHeight: 1.2;
$headingLetterSpacing: 0.1rem;
$headingTextTransform: uppercase;
//$headingTextShadow: none;
$headingFontWeight: 300;
//$heading1TextShadow: $headingTextShadow;

$heading1Size: 2.0em;
$heading2Size: 1.6em;
$heading3Size: 1.3em;
$heading4Size: 1.0em;

//$codeFont: monospace;

// Links and actions
$linkColor: #9f613b;
$linkColorHover: lighten( $linkColor, 15% );

// Text selection
$selectionBackgroundColor: lighten( $linkColor, 25% );
//$selectionColor: #fff;

